import React from 'react';
import { Link } from 'react-router-dom';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { 
  CheckCircle, 
  Crown, 
  Star, 
  Zap, 
  Sparkles, 
  ArrowLeft,
  Calendar,
  Infinity,
  Clock,
  MessageCircle
} from 'lucide-react';
import { useWhatsAppSettings, createWhatsAppLink as createWhatsAppLinkUtil } from '../utils/whatsappService';
import { loadContentFromFirebase as loadPlans } from '@/utils/contentService';

const SubscriptionsPage: React.FC = () => {
  const { settings } = useWhatsAppSettings();
  const [content, setContent] = React.useState<any>(null);
  React.useEffect(() => {
    (async () => {
      try {
        const data = await loadPlans();
        setContent(data);
      } catch (e) {
        console.warn('Failed to load subscription content, trying local cache');
        try {
          const cached = localStorage.getItem('siteContent_plans_cache');
          if (cached) {
            setContent(JSON.parse(cached));
          } else {
            setContent(null);
          }
        } catch {
          setContent(null);
        }
      }
    })();
  }, []);
  
  // دالة لإنشاء رابط الواتساب مع رسالة مخصصة
  const createWhatsAppLink = (planName: string, price: string, features: string[]) => {
    if (!settings) return '#';
    
    const message = `مرحباً! أريد الاشتراك في باقة ${planName} بسعر ${price} جنيه

المميزات المشمولة:
${features.map(feature => `✅ ${feature}`).join('\n')}

أرجو التواصل معي لإتمام عملية الاشتراك.`;
    
    return createWhatsAppLinkUtil(settings.phoneNumber, message);
  };
  const plans = React.useMemo(() => {
    // Helper to override old prices from Firestore
    const overridePrice = (plan: any, oldPrice: string, newPrice: string) => {
      if (plan.price === oldPrice || plan.price === `${oldPrice} جنيه`) {
        return { ...plan, price: newPrice };
      }
      return plan;
    };

    let monthly = content?.monthly || {
      name: 'باقه شهرية',
      price: '200',
      period: 'شهر',
      description: 'باقة شهرية مع كاشير واحد',
      features: [
        'إمكانية إضافة كاشير 1',
        'عدد محافظ غير محدود',
        'خدمة عملاء متوسطة'
      ]
    };
    // Fix old price 150 -> 200
    monthly = overridePrice(monthly, '150', '200');

    let quarterly = content?.quarterly || {
      name: 'باقة ربع سنوية',
      price: '450',
      period: '3 شهور',
      description: 'باقة ربع سنوية مع مميزات إضافية',
      features: [
        'إمكانية إضافة كاشير 1',
        'عدد محافظ غير محدود',
        'خدمة عملاء مميزة',
        'توفير مقارنة بالباقة الشهرية'
      ]
    };
    // Force fix price if it matches old values
    quarterly = overridePrice(quarterly, '350', '450');

    const halfYearly = content?.halfYearly || {
      name: 'باقة نصف سنوية',
      price: '750',
      period: '6 شهور',
      description: 'باقة نصف سنوية مع مميزات متقدمة',
      features: [
        'دعم ممتاز',
        'عدد محافظ مفتوح',
        'التحويل المباشر من التطبيق',
        'استقبال السحب والتحويل الاوتوماتيك'
      ]
    };
    let yearly = content?.yearly || {
      name: 'باقه سنوية',
      price: '1450',
      period: 'سنة',
      description: 'باقة سنوية مع 2 كاشير',
      features: [
        'عدد 2 كاشير',
        'عدد محافظ غير محدود',
        'خدمة عملاء سريعة',
        'إمكانية طلب إضافة مميزات لحساب المستخدم'
      ]
    };
    // Fix old price 1350 -> 1450
    yearly = overridePrice(yearly, '1350', '1450');
    // Also fix 1250 -> 1450 (as seen in screenshot)
    yearly = overridePrice(yearly, '1250', '1450');

    const lifetime = content?.lifetime || {
      name: 'باقه مدى الحياة',
      price: '4000',
      period: 'مدى الحياة',
      description: 'مدى الحياة مع نفس مميزات الباقة السنوية',
      features: [
        'عدد 2 كاشير',
        'عدد محافظ غير محدود',
        'خدمة عملاء سريعة',
        'إمكانية طلب إضافة مميزات لحساب المستخدم',
        'دفعة واحدة مدى الحياة'
      ]
    };

    // Force correct prices and names regardless of Firestore data
    monthly.price = '200';
    monthly.name = 'الباقة الشهرية';
    
    quarterly.price = '450';
    quarterly.name = 'الباقة الربع سنوية';
    quarterly.period = '3 شهور';

    yearly.price = '1450';
    yearly.name = 'الباقة السنوية';
    yearly.period = 'سنة';

    lifetime.price = '4000';
     lifetime.name = 'الباقة مدى الحياة';
     lifetime.description = 'استثمار واحد لمدي الحياه';

     return [
      {
        id: 'monthly-1-cashier',
        name: monthly.name,
        price: monthly.price,
        period: monthly.period,
        description: monthly.description,
        features: monthly.features,
        icon: Calendar,
        color: 'from-indigo-500 to-purple-500',
        popular: false,
      },
      {
        id: 'quarterly-1-cashier',
        name: quarterly.name,
        price: quarterly.price,
        period: quarterly.period,
        description: quarterly.description,
        features: quarterly.features,
        icon: Clock,
        color: 'from-blue-500 to-cyan-500',
        popular: false,
      },
      {
        id: 'half-yearly',
        name: halfYearly.name,
        price: halfYearly.price,
        period: halfYearly.period,
        description: halfYearly.description,
        features: halfYearly.features,
        icon: Star,
        color: 'from-orange-500 to-amber-500',
        popular: true,
      },
      {
        id: 'yearly-2-cashiers',
        name: yearly.name,
        price: yearly.price,
        period: yearly.period,
        description: yearly.description,
        features: yearly.features,
        icon: Star,
        color: 'from-teal-500 to-emerald-500',
        popular: true,
      },
      {
        id: 'lifetime-4-cashiers',
        name: lifetime.name,
        price: lifetime.price,
        period: lifetime.period,
        description: lifetime.description,
        features: lifetime.features,
        icon: Infinity,
        color: 'from-rose-500 to-red-500',
        popular: false,
      },
    ];
  }, [content]);

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="bg-white/80 backdrop-blur-sm border-b sticky top-0 z-50">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <Link to="/" className="flex items-center gap-2 text-primary hover:text-primary/80 transition-colors">
              <ArrowLeft className="h-5 w-5" />
              <span className="font-medium">العودة للصفحة الرئيسية</span>
            </Link>
            <div className="text-2xl font-bold gradient-hero bg-clip-text text-transparent">
              CashyLink
            </div>
          </div>
        </div>
      </header>

      {/* Hero Section */}
      <section className="container mx-auto px-4 py-16 text-center relative">
        {/* Background Animation */}
        <div className="absolute inset-0 overflow-hidden pointer-events-none">
          <div className="absolute top-20 left-10 w-32 h-32 bg-blue-200 rounded-full blur-3xl opacity-30 animate-pulse"></div>
          <div className="absolute top-40 right-20 w-40 h-40 bg-purple-200 rounded-full blur-3xl opacity-30 animate-pulse" style={{animationDelay: '1s'}}></div>
          <div className="absolute bottom-20 left-1/3 w-36 h-36 bg-pink-200 rounded-full blur-3xl opacity-30 animate-pulse" style={{animationDelay: '2s'}}></div>
        </div>

        <div className="relative z-10">
          <div className="inline-flex items-center gap-2 bg-primary/10 text-primary px-6 py-3 rounded-full text-sm font-medium mb-8 animate-fade-in-up">
            <Sparkles className="h-5 w-5" />
            اختر الباقة المناسبة لك
          </div>

          <h1 className="text-5xl md:text-7xl font-bold mb-8 gradient-hero bg-clip-text text-transparent animate-fade-in-up" style={{animationDelay: '0.2s'}}>
            باقات الاشتراك
          </h1>
          
          <p className="text-xl md:text-2xl text-muted-foreground mb-12 max-w-3xl mx-auto leading-relaxed animate-fade-in-up" style={{animationDelay: '0.4s'}}>
            اختر الباقة التي تناسب احتياجاتك وابدأ رحلتك مع CashyLink اليوم
          </p>

          {/* Trust Indicators */}
          <div className="flex flex-wrap justify-center items-center gap-8 text-muted-foreground animate-fade-in-up" style={{animationDelay: '0.6s'}}>
            <div className="flex items-center gap-2">
              <CheckCircle className="h-5 w-5 text-green-500" />
              <span>إلغاء في أي وقت</span>
            </div>
            <div className="flex items-center gap-2">
              <CheckCircle className="h-5 w-5 text-green-500" />
              <span>دعم فني متميز</span>
            </div>
          </div>
        </div>
      </section>

      {/* Pricing Cards */}
      <section className="container mx-auto px-4 pb-20">
        <div className="grid grid-cols-2 sm:grid-cols-2 lg:grid-cols-3 gap-4 max-w-screen-xl mx-auto">
          {plans.map((plan, index) => {
            const IconComponent = plan.icon;
            return (
              <Card 
                key={plan.id} 
                className={`relative overflow-hidden transform hover:scale-105 transition-all duration-500 h-full flex flex-col ${
                  plan.popular ? 'ring-2 ring-primary shadow-2xl' : 'shadow-xl hover:shadow-2xl'
                } animate-fade-in-up`}
                style={{animationDelay: `${index * 0.2}s`}}
              >
                {plan.popular && (
                  <div className="absolute top-0 left-0 right-0">
                    <div className="bg-gradient-to-r from-primary to-purple-600 text-white text-center py-2 text-sm font-bold">
                      الأكثر شعبية ⭐
                    </div>
                  </div>
                )}

                <CardHeader className={`bg-gradient-to-br ${plan.color} text-white relative ${plan.popular ? 'pt-8' : 'pt-6'}`}>
                  <div className="absolute top-3 right-3">
                    <IconComponent className="h-7 w-7 text-white/80" />
                  </div>
                  
                  <CardTitle className="text-base font-bold mb-1">{plan.name}</CardTitle>
                  <CardDescription className="text-white/90 text-sm">
                    {plan.description}
                  </CardDescription>
                  
                  <div className="mt-4">
                    <div className="flex items-baseline gap-2">
                      <span className="text-3xl font-bold">{plan.price}</span>
                      <span className="text-white/80 text-xs">جنيه</span>
                    </div>
                    <div className="text-white/80 text-xs mt-1">
                      {plan.period === 'مدى الحياة' ? 'دفعة واحدة' : `لكل ${plan.period}`}
                    </div>
                  </div>
                </CardHeader>

                <CardContent className="p-4 flex-1">
                  <ul className="space-y-2">
                    {plan.features.map((feature, featureIndex) => (
                      <li key={featureIndex} className="flex items-start gap-2">
                        <CheckCircle className="h-4 w-4 text-green-500 flex-shrink-0 mt-0.5" />
                        <span className="text-sm text-foreground">{feature}</span>
                      </li>
                    ))}
                  </ul>
                </CardContent>

                <CardFooter className="p-4 pt-0">
                  <a
                    href={createWhatsAppLink(plan.name, plan.price, plan.features)}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="w-full"
                  >
                    <Button 
                      className={`w-full h-12 text-lg font-medium shadow-lg hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1 bg-gradient-to-r ${plan.color}`}
                    >
                      <MessageCircle className="h-5 w-5 ml-2" />
                      اشترك الآن
                    </Button>
                  </a>
                </CardFooter>
              </Card>
            );
          })}
        </div>

        {/* Additional Info */}
        <div className="mt-16 text-center">
          <div className="bg-white/80 backdrop-blur-sm rounded-3xl p-8 max-w-4xl mx-auto shadow-xl">
            <h3 className="text-2xl font-bold mb-6 text-foreground">
              لديك أسئلة حول الباقات؟
            </h3>
            <p className="text-muted-foreground mb-8 text-lg">
              فريقنا جاهز لمساعدتك في اختيار الباقة المناسبة لاحتياجاتك
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Button variant="outline" size="lg" className="font-bold">
                تواصل معنا
              </Button>
              <Button variant="ghost" size="lg" className="font-bold">
                مقارنة الباقات
              </Button>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default SubscriptionsPage;
